.social-btns{
    margin-bottom: 0;
    padding-left: 0;
    list-style: none;
}

.inline-list-item{
    margin-right: 1rem;
    display: inline-block;
}

.social-btns li a{
    text-decoration: none;
    font-size: 25px;
    line-height: 50px;
    display: block;
    width: 50px;
    height: 50px;
    -webkit-transition: all .3s;
    transition: all .3s;
    color: #212529;
    border-radius: 100%;
    outline: 0;
    background-color: #fff;
}

.social-btns li a:hover{
    background-color: #f05f40;
}

